import React, { useRef, useEffect, useState } from "react";
import { Scene, SceneOverlay, SceneBg, ParallaxWrapper } from "../ui/modules";
import { Container } from "../ui/elements";
import Parallax from "parallax-js";
import Sound from "react-sound";
import { SoundToggler } from "../SoundToggler/SoundToggler";
import { useSelector } from "react-redux";
import { selectMuted } from "../../_redux/slices/soundSlice";
import { selectFontsLoaded } from "../../_redux/slices/utilsSlice";

export const SceneTpl = ({ bg, sound, children, className }) => {
  const wrapperEl = useRef(null);

  const [bgLoaded, setBgLoaded] = useState(false);
  // const [soundLoaded, setSoundLoaded] = useState(false);

  const muted = useSelector(selectMuted);
  const fontsLoaded = useSelector(selectFontsLoaded);

  useEffect(() => {
    const parallax = new Parallax(wrapperEl.current, {});
    return () => {
      parallax.disable();
    };
  }, []);

  const mediaLoaded = bgLoaded && fontsLoaded;

  return (
    <Scene>
      <SoundToggler />
      <Sound
        url={sound}
        // onLoad={() => setSoundLoaded(true)}
        playStatus={!muted ? "PLAYING" : "PAUSED"}
        volume={30}
        loop
      />
      <SceneOverlay />
      <ParallaxWrapper ref={wrapperEl}>
        <SceneBg src={bg} data-depth="0.10" onLoad={() => setBgLoaded(true)} />
      </ParallaxWrapper>

      {mediaLoaded && (
        <Container
          className={`content ${
            mediaLoaded ? "media-loaded" : "media-not-loaded"
          }`}
        >
          {children}
        </Container>
      )}
    </Scene>
  );
};
