import React, { useState, useEffect } from "react";
import { Random } from "react-animated-text";
import styled from "styled-components";
// import TextyAnim from "rc-texty";

export const AnimatedTextBox = styled(
  ({ children, paused, className, data }) => {
    // const [slides, setSlides] = useState(data);
    const [currentSlide, setCurrentSlide] = useState(data[0]);

    // const [showBtns, setShowBtns] = useState(false);

    let interval = setTimeout(() => changeSlide(), 5000);

    const changeSlide = () => {
      clearTimeout(interval);
      interval = setTimeout(() => changeSlide(), 7000);
      const idx = data.findIndex(el => el === currentSlide);
      const next = data[data.findIndex(el => el === currentSlide) + 1];
      console.log(next);
      if (idx < data.length - 1) setCurrentSlide(next);
      else {
        // setShowBtns(true);
        clearTimeout(interval);
      }
    };

    useEffect(() => {
      return () => clearTimeout(interval);
    }, [interval]);

    return (
      <div className={className} onClick={changeSlide}>
        <Random
          text={currentSlide}
          paused={false}
          iterations={1}
          effect="verticalFadeIn"
          effectChange={2}
          effectDirection="up"
        />
        {/* <TextyAnim onEnd={changeSlide}>{currentSlide}</TextyAnim> */}
      </div>
    );
  }
)`
  font-size: 6rem;
  color: white;
`;
