import React from "react";
import menuSound from "../../../sounds/menu.mp3";
import menuSoundClick from "../../../sounds/menu-click-2.mp3";
import UIfx from "uifx";
import { useSelector } from "react-redux";
import { selectMuted } from "../../../_redux/slices/soundSlice";

const menuHover = new UIfx(menuSound, {
  volume: 0.2, // number between 0.0 ~ 1.0
  throttleMs: 100
});
const menuClick = new UIfx(menuSoundClick, {
  volume: 0.2, // number between 0.0 ~ 1.0
  throttleMs: 100
});

export const SoundedBtn = ({ children, className, disabled, ...props }) => {
  const muted = useSelector(selectMuted);
  const disabledOrMuted = muted || disabled;

  return (
    <div
      {...props}
      className={className}
      onMouseEnter={() => disabledOrMuted || menuHover.play()}
      onClick={() => disabledOrMuted || menuClick.play()}
    >
      {children}
    </div>
  );
};
