import React from "react";
import styled from "styled-components";
// import { AccessAlarm, ThreeDRotation } from "@material-ui/icons";
import VolumeUpIcon from "@material-ui/icons/VolumeUp";
import VolumeOffIcon from "@material-ui/icons/VolumeOff";
import { useDispatch, useSelector } from "react-redux";
import { setMuted, selectMuted } from "../../_redux/slices/soundSlice";

const SoundTogglerWithoutHoc = styled(({ className }) => {
  const muted = useSelector(selectMuted);

  const dispatch = useDispatch();

  return (
    <div className={className} onClick={() => dispatch(setMuted(!muted))}>
      {!muted ? <VolumeUpIcon /> : <VolumeOffIcon />}
    </div>
  );
})`
  position: fixed;
  top: 2rem;
  right: 2rem;
  width: 4rem;
  height: 4rem;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: ${props => props.theme.colors.light};
  z-index: 3;
  cursor: pointer;
  transition: 0.3s;
  &:hover {
    transform: scale(1.05);
  }
`;

export const SoundToggler = SoundTogglerWithoutHoc;
