const TIMEOUT = 2000;

export const timeout = f => setTimeout(f, TIMEOUT);

export const getArrayFromText = (text, maxLength) => {
  const textTrimmed = text.replace(/[/\n]/g, "");

  const arr = [[]];

  textTrimmed.split(" ").forEach((el, idx) => {
    if (el === "") return;
    if (arr[arr.length - 1].length < maxLength) arr[arr.length - 1].push(el);
    else {
      arr.push([]);
      arr[arr.length - 1].push(el);
    }
  });

  return arr;
};
